@import url("http://fonts.googleapis.com/earlyaccess/notosanskr.css");
.project-cardview {
  width: 360px;
  height: 440px;
  background-color: #ffffff;
  display: inline-block;
  margin-right: 2.8%;
  margin-bottom: 49px; }

.project-cardview:hover {
  box-shadow: 5px 7px 33px 0 rgba(0, 0, 0, 0.16); }

.project-cardview-banner {
  width: 360px;
  height: 135px;
  object-fit: cover; }

.jobgroup {
  margin-top: 29px;
  height: 29px;
  width: 360px; }

.jobgroup-circle {
  width: 14px;
  height: 14px;
  background-color: #66efe2;
  border-radius: 7px;
  margin-top: 9px;
  margin-bottom: 6px;
  display: inline-block;
  vertical-align: text-top; }

.jobgroup-main {
  height: 29px;
  font-family: "Noto Sans KR";
  font-size: 20px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.45;
  letter-spacing: normal;
  text-align: left;
  color: #333333;
  margin-left: 10px;
  display: inline-block;
  vertical-align: text-top; }

.jobgroup-sub {
  height: 22px;
  font-family: "Noto Sans KR";
  font-size: 15px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.47;
  letter-spacing: normal;
  text-align: left;
  color: #333333;
  margin-left: 9px;
  margin-top: 4px;
  margin-bottom: 3px;
  display: inline-block;
  vertical-align: text-top; }

.project-cardview-main {
  height: 29px;
  font-family: "Noto Sans KR";
  font-size: 20px;
  font-weight: 500;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.45;
  letter-spacing: normal;
  text-align: left;
  color: #333333;
  margin-top: 39px; }

.project-cardview-contents {
  margin-left: 32px; }

.project-cardview-step {
  height: 24px;
  font-family: "Noto Sans KR";
  font-size: 16px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.5;
  letter-spacing: normal;
  text-align: left;
